* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

a {
  text-decoration: none;
  color: inherit; }

body {
  margin: 0; }

header {
  position: fixed;
  top: 0;
  height: 50px;
  background: #224e0f;
  width: 100%; }
  header a {
    line-height: 50px;
    vertical-align: middle;
    padding-left: 15px; }
  header img {
    height: 40px;
    vertical-align: middle; }

#root {
  margin: 100px auto;
  padding: 20px;
  width: 80%;
  font-family: "Roboto", sans-serif; }

table {
  table-layout: fixed;
  width: 100%;
  border-radius: 5px;
  border-collapse: collapse; }

thead th {
  color: #d5dde5;
  letter-spacing: 1px;
  background: #1b1e24;
  padding: 12px 2px;
  font-size: 24px;
  font-weight: 100;
  border-right: 1px solid #343a45;
  text-rendering: optimizeLegibilty; }

thead th:nth-child(1) {
  width: 40px;
  border-top-left-radius: 5px; }

thead th:nth-child(2) {
  width: 40%; }

thead th:nth-child(3) {
  width: 25%; }

thead th:nth-child(4) {
  width: 25%;
  border-top-right-radius: 5px;
  border-right: none; }

thead th:hover {
  cursor: pointer; }

tbody tr:nth-child(odd) {
  background: #ebebeb; }

tbody tr:nth-child(even) {
  background: #ffffff; }

tbody td {
  padding: 10px 15px;
  border: 1px solid #c8c8c8; }

footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: white;
  background: #224e0f;
  height: 50px;
  font-family: "saxMono", monospace, sans-serif;
  font-size: 18px; }



